import each from 'lodash/each'
import genPm from 'wsemi/src/genPm.mjs'
import isstr from 'wsemi/src/isstr.mjs'


/**
 * RouteshapiAPI
 *
 * @param {Array} [apis=[]] hapiapisroutesapis[]
 * @param {Array} [routes=[]] routes'table''props'[]
 * @param {String} [apiParent='api'] api'api'
 * @param {Function} [proc=() => {}] apipropmethod(API method),table(),propName(),propValue(),payload(post),req(hapireq),res(hapires),pm(Promise)pm.resolvepm.reject()=>{}
 * @returns {Array} hapiAPI
 */
function routesToAPI(apis = [], routes = [], apiParent = '', proc = () => {}) {

    //basePath
    let basePath = `/`
    if (isstr(apiParent)) {
        basePath = `/${apiParent}/`
    }

    //each routes
    each(routes, (v) => {
        let r

        //GET all
        r = {
            path: basePath + v.table,
            method: 'GET',
            handler: function (req, res) {
                let pm = genPm()

                proc({
                    method: 'GET',
                    table: v.table,
                    propName: null,
                    propValue: null,
                    payload: null,
                    req,
                    res,
                    pm,
                })

                return pm
            },
        }
        apis.push(r)

        //GET props
        each(v.props, (vv, col) => {
            r = {
                path: basePath + v.table + `/${col}/{${col}}`,
                method: 'GET',
                handler: function (req, res) {
                    let pm = genPm()

                    let p = req.params

                    proc({
                        method: 'GET',
                        table: v.table,
                        propName: col,
                        propValue: p[col],
                        payload: null,
                        req,
                        res,
                        pm,
                    })

                    return pm
                },
            }
            apis.push(r)
        })

        //POST, PUT
        each(['POST', 'PUT'], (method) => {

            //POST, PUT all
            r = {
                path: basePath + v.table,
                method,
                handler: function (req, res) {
                    let pm = genPm()

                    let data = req.payload

                    proc({
                        method,
                        table: v.table,
                        propName: null,
                        propValue: null,
                        payload: data,
                        req,
                        res,
                        pm,
                    })

                    return pm
                },
            }
            apis.push(r)

            //POST, PUT props
            each(v.props, (vv, col) => {
                r = {
                    path: basePath + v.table + `/${col}/{${col}}`,
                    method,
                    handler: function (req, res) {
                        let pm = genPm()

                        let p = req.params
                        let data = req.payload

                        proc({
                            method,
                            table: v.table,
                            propName: col,
                            propValue: p[col],
                            payload: data,
                            req,
                            res,
                            pm,
                        })

                        return pm
                    },
                }
                apis.push(r)
            })

        })

        //DELETE all

        //DELETE props
        each(v.props, (vv, col) => {
            r = {
                path: basePath + v.table + `/${col}/{${col}}`,
                method: 'DELETE',
                handler: function (req, res) {
                    let pm = genPm()

                    let p = req.params

                    proc({
                        method: 'DELETE',
                        table: v.table,
                        propName: col,
                        propValue: p[col],
                        payload: null,
                        req,
                        res,
                        pm,
                    })

                    return pm
                },
            }
            apis.push(r)
        })

    })

    return apis
}


export default routesToAPI
