import fs from 'fs'
import Hapi from '@hapi/hapi'
import Inert from '@hapi/inert' //
import sortBy from 'lodash/sortBy'
import isfun from 'wsemi/src/isfun.mjs'
import routesToSwagger from './routesToSwagger.mjs'
import routesToAPI from './routesToAPI.mjs'


/**
 * http API
 *
 * @param {Object} opt 
 * @param {Object} [opt.serverHapi=null] hapiserverHapi.routeapisnull
 * @param {Integer} [opt.port=8080] APIport8080
 * @param {String} [opt.docFolder='swdoc'] swagger'swdoc'[swdoc.7z]
 * @param {String} [opt.apiParent='api'] api'api'
 * @param {Object} [opt.funcs={}] keyvaluePromiseresolvereject{}
 * @param {Array} [opt.routes=[]] routes[]
 * @param {function} [opt.proc=null] apinull
 */
function WRestapi(opt) {

    //default
    if (!opt.port) {
        opt.port = 8080
    }
    if (!opt.docFolder) {
        opt.docFolder = 'swdoc'
    }
    if (!opt.apiParent) {
        opt.apiParent = 'api'
    }
    if (!opt.routes) {
        opt.routes = []
    }
    if (!isfun(opt.proc)) {
        opt.proc = () => {}
    }

    //apiFile
    let apiFile = {
        method: 'GET',
        path: `/${opt.docFolder}/{file*}`,
        handler: {
            directory: {
                path: `${opt.docFolder}/`
            }
        },
    }

    //apis
    let apis = []
    apis.push(apiFile)

    //sortBy
    opt.routes = sortBy(opt.routes, 'table')

    //routesToAPI
    apis = routesToAPI(apis, opt.routes, opt.apiParent, opt.proc)

    //routesToSwagger
    let sw = routesToSwagger(`localhost:${opt.port}`, opt.apiParent, opt.routes)
    fs.writeFileSync(`./${opt.docFolder}/swagger.json`, JSON.stringify(sw, null, 4), 'utf8')
    //console.log(sw)

    async function runRestfulServer() {

        //server
        let server = Hapi.server({
            port: opt.port,
            //host: 'localhost',
            routes: {
                cors: true
            },
        })

        //register inert
        await server.register(Inert)

        //route apis
        server.route(apis)

        //start
        server.start()

        console.log(`Server running at: ${server.info.uri}`)
        console.log(`Server for API Documents running at: ${server.info.uri}/${opt.docFolder}/index.html`)

    }

    if (opt.serverHapi) {
        opt.serverHapi.route(apis)
    }
    else {
        runRestfulServer()
    }

}


export default WRestapi
